<template>
  <div id="app">
    <mu-appbar>
      <mu-flat-button label="O ' MyCollege" slot="left" @click="backToHome"/>

      <mu-flat-button v-if="!logTag" @click="dialog = true"
                      label="" slot="right" primary/>

      <mu-icon-button v-if="logTag" icon='add' slot="right" @click="editPost" tooltip="" touch/>
      <mu-badge v-if="logTag" :content="unRead" circle secondary slot="right">
        <mu-icon-button icon="mail" @click="goMessageList"/>
      </mu-badge>
      <mu-icon-menu v-if="logTag" icon="account_circle" slot="right" :anchorOrigin="anchorOrigin"
                    :targetOrigin="targetOrigin">
        <mu-menu-item title="" leftIcon="home" @click="goUserPage"/>

        <mu-divider/>
        <mu-menu-item title="" leftIcon="settings" @click="toSettings"/>

        <mu-divider/>
        <mu-menu-item title="" leftIcon="exit_to_app" @click="sighOut"/>

      </mu-icon-menu>
    </mu-appbar>

    <mu-dialog :open="dialog" title="" @close="dialog = false">
      <mu-sub-header>{{loginError}}</mu-sub-header>
      <mu-text-field v-model="loginUser.username" label="" hintText="" type="email" labelFloat/>
      <br/>
      <mu-text-field v-model="loginUser.password" label="" hintText="" type="password" labelFloat/>
      <br/>
      <mu-flat-button label="" slot="actions" @click="openSignup"/>
      <mu-flat-button label="" slot="actions" primary @click="signin"/>

    </mu-dialog>
    <mu-dialog :open="signupTag" title="">
      <mu-sub-header>{{signupError}}</mu-sub-header>

      <mu-text-field v-model="signupUser.username" label="" hintText="" type="email" labelFloat/>
      <br/>
      <mu-text-field v-model="signupUser.password" label="" hintText="" type="password" labelFloat/>
      <br/>
      <mu-text-field v-model="signupUser.verfiPass" label="" hintText="" type="password" labelFloat/>
      <br/>
      <mu-flat-button label="" slot="actions" @click="signupTag = false"/>
      <mu-flat-button label="" slot="actions" primary @click="signup"/>

    </mu-dialog>

    <router-view></router-view>
  </div>
</template>
</div>
</template>

<script>
  import {mapState, mapMutations} from 'vuex'
  export default {
    name: 'app',
    components: {},
    data () {
      return {
        activeTab: 'latest',
        anchorOrigin: {"vertical": "bottom", "horizontal": "left"},
        targetOrigin: {"vertical": "top", "horizontal": "left"},
        dialog: false,
        loginUser: {
          username: null,
          password: null
        },
        signupUser: {
          username: null,
          password: null,
          verfiPass: null
        },
        signupTag: false
      }
    },
    methods: {
      goMessageList () {
        this.$router.push({path: '/message'})
      },
      goFollowPage () {
        this.$router.push({path: '/user/' + this.user.id + '/follow'})
      },
      goLikePage () {
        this.$router.push({path: '/user/' + this.user.id + '/like'})
      },
      toSettings () {
        this.$router.push({name: 'settings'})
      },
      backToHome () {
        this.$router.replace('/')
      },
      goUserPage () {
        this.$router.push({path: '/user/' + this.user.id})
      },
      editPost () {
        this.$router.push({name: 'postedit'})
      },
      signin () {
        this.$store.dispatch('sendLoginReq', this.loginUser)
      },
      openSignup () {
        this.dialog = false;
        this.signupTag = true
      },
      signup () {
        this.$store.dispatch('sendSignupReq', this.signupUser)
      },
      sighOut () {
        this.$store.commit('clearCurrentUser');
        this.$router.replace('/')
      },
      ...mapMutations([
        'checkLocalUser',
      ])
    },
    created: function () {
      this.$store.dispatch('fetchUnreadMessage');
      this.checkLocalUser();
    },
    computed: {
      ...mapState([
        'logTag', 'loginError', 'signupError', 'user', 'unRead'
      ])
    },
    watch: {
      logTag (val) {
        if (val == true) {
          this.dialog = false
          this.signupTag = false
        }
      }
    }
  }
</script>

<style>

</style>



